import { isBigInt, isBigNumber } from '../../utils/is.ts'
import { format, sign, nearlyEqual } from '../../utils/number.ts'
import { factory } from '../../utils/factory.ts'

// BigNumber type (avoid circular dependency)
interface BigNumber {
  toNumber(): number
}

const name = 'Range'
const dependencies: string[] = []

/**
 * Callback function for Range forEach operations
 */
export type RangeForEachCallback = (
  value: number,
  index: number[],
  range: any
) => void

/**
 * Callback function for Range map operations
 */
export type RangeMapCallback<T> = (
  value: number,
  index: number[],
  range: any
) => T

/**
 * Formatting options for Range display
 */
export interface RangeFormatOptions {
  precision?: number
  notation?: 'fixed' | 'exponential' | 'engineering' | 'auto'
  [key: string]: any
}

/**
 * JSON representation of a Range
 */
export interface RangeJSON {
  mathjs: 'Range'
  start: number
  end: number
  step: number
}

export const createRangeClass = /* #__PURE__ */ factory(
  name,
  dependencies,
  () => {
    /**
     * Create a range of numbers. A range has a start, step, and end,
     * and contains functions to iterate over the range.
     *
     * A range can be constructed as:
     *
     *     const range = new Range(start, end)
     *     const range = new Range(start, end, step)
     *
     * Note that the endpoints and step may be specified with other numeric
     * types such as bigint or BigNumber, but they will be demoted to the
     * built-in `number` type and the Range will only contain numbers. The
     * rationale for this demotion is that Range objects are primarily used
     * for indexing Matrix objects, and Matrix objects may only be indexed
     * with `number`s.
     *
     * To get the result of the range:
     *     range.forEach(function (x) {
     *         console.log(x)
     *     })
     *     range.map(function (x) {
     *         return math.sin(x)
     *     })
     *     range.toArray()
     *
     * Example usage:
     *
     *     const c = new Range(2, 6)       // 2:1:5
     *     c.toArray()                     // [2, 3, 4, 5]
     *     const d = new Range(2, -3, -1)  // 2:-1:-2
     *     d.toArray()                     // [2, 1, 0, -1, -2]
     *
     * @class Range
     * @constructor Range
     * @param {number} start included lower bound
     * @param {number} end excluded upper bound
     * @param {number} [step] step size, default value is 1
     */
    class Range {
      /**
       * Type identifier
       */
      readonly type: string = 'Range'

      /**
       * Range type flag
       */
      readonly isRange: boolean = true

      /**
       * Start value of the range (inclusive)
       */
      start: number

      /**
       * End value of the range (exclusive)
       */
      end: number

      /**
       * Step size for the range
       */
      step: number

      constructor(
        start?: number | bigint | BigNumber | null,
        end?: number | bigint | BigNumber | null,
        step?: number | bigint | BigNumber | null
      ) {
        if (!(this instanceof Range)) {
          throw new SyntaxError(
            'Constructor must be called with the new operator'
          )
        }

        const hasStart = start !== null && start !== undefined
        const hasEnd = end !== null && end !== undefined
        const hasStep = step !== null && step !== undefined

        let startValue: number | bigint = 0
        let endValue: number | bigint = 0
        let stepValue: number | bigint = 1

        if (hasStart) {
          if (isBigNumber(start)) {
            startValue = (start as BigNumber).toNumber()
          } else if (typeof start !== 'number' && !isBigInt(start)) {
            throw new TypeError('Parameter start must be a number or bigint')
          } else {
            startValue = start as number | bigint
          }
        }
        if (hasEnd) {
          if (isBigNumber(end)) {
            endValue = (end as BigNumber).toNumber()
          } else if (typeof end !== 'number' && !isBigInt(end)) {
            throw new TypeError('Parameter end must be a number or bigint')
          } else {
            endValue = end as number | bigint
          }
        }
        if (hasStep) {
          if (isBigNumber(step)) {
            stepValue = (step as BigNumber).toNumber()
          } else if (typeof step !== 'number' && !isBigInt(step)) {
            throw new TypeError('Parameter step must be a number or bigint')
          } else {
            stepValue = step as number | bigint
          }
        }

        this.start = hasStart ? parseFloat(startValue.toString()) : 0
        this.end = hasEnd ? parseFloat(endValue.toString()) : 0
        this.step = hasStep ? parseFloat(stepValue.toString()) : 1
        if (hasStep && nearlyEqual(this.step, 0)) {
          throw new Error('Step must not be zero')
        }
      }

      /**
       * Parse a string into a range,
       * The string contains the start, optional step, and end, separated by a colon.
       * If the string does not contain a valid range, null is returned.
       * For example str='0:2:11'.
       * @memberof Range
       * @param {string} str
       * @return {Range | null} range
       */
      static parse(str: string): Range | null {
        if (typeof str !== 'string') {
          return null
        }

        const args = str.split(':')
        const nums = args.map(function (arg) {
          return parseFloat(arg)
        })

        const invalid = nums.some(function (num) {
          return isNaN(num)
        })
        if (invalid) {
          return null
        }

        switch (nums.length) {
          case 2:
            return new Range(nums[0], nums[1])
          case 3:
            return new Range(nums[0], nums[2], nums[1])
          default:
            return null
        }
      }

      /**
       * Create a clone of the range
       * @return {Range} clone
       */
      clone(): Range {
        return new Range(this.start, this.end, this.step)
      }

      /**
       * Retrieve the size of the range.
       * Returns an array containing one number, the number of elements in the range.
       * @memberof Range
       * @returns {number[]} size
       */
      size(): number[] {
        let len = 0
        const start = this.start
        const step = this.step
        const end = this.end
        const diff = end - start

        if (sign(step) === sign(diff)) {
          len = Math.ceil(diff / step)
        } else if (diff === 0) {
          len = 0
        }

        if (isNaN(len)) {
          len = 0
        }
        return [len]
      }

      /**
       * Calculate the minimum value in the range
       * @memberof Range
       * @return {number | undefined} min
       */
      min(): number | undefined {
        const size = this.size()[0]

        if (size > 0) {
          if (this.step > 0) {
            // positive step
            return this.start
          } else {
            // negative step
            return this.start + (size - 1) * this.step
          }
        } else {
          return undefined
        }
      }

      /**
       * Calculate the maximum value in the range
       * @memberof Range
       * @return {number | undefined} max
       */
      max(): number | undefined {
        const size = this.size()[0]

        if (size > 0) {
          if (this.step > 0) {
            // positive step
            return this.start + (size - 1) * this.step
          } else {
            // negative step
            return this.start
          }
        } else {
          return undefined
        }
      }

      /**
       * Execute a callback function for each value in the range.
       * @memberof Range
       * @param {function} callback The callback method is invoked with three
       *                            parameters: the value of the element, the index
       *                            of the element, and the Range being traversed.
       */
      forEach(callback: RangeForEachCallback): void {
        let x = this.start
        const step = this.step
        const end = this.end
        let i = 0

        if (step > 0) {
          while (x < end) {
            callback(x, [i], this)
            x += step
            i++
          }
        } else if (step < 0) {
          while (x > end) {
            callback(x, [i], this)
            x += step
            i++
          }
        }
      }

      /**
       * Execute a callback function for each value in the Range, and return the
       * results as an array
       * @memberof Range
       * @param {function} callback The callback method is invoked with three
       *                            parameters: the value of the element, the index
       *                            of the element, and the Matrix being traversed.
       * @returns {Array} array
       */
      map<T>(callback: RangeMapCallback<T>): T[] {
        const array: T[] = []
        const self = this
        this.forEach(function (value, index, _obj) {
          array[index[0]] = callback(value, index, self)
        })
        return array
      }

      /**
       * Create an Array with a copy of the Ranges data
       * @memberof Range
       * @returns {Array} array
       */
      toArray(): number[] {
        const array: number[] = []
        this.forEach(function (value, index) {
          array[index[0]] = value
        })
        return array
      }

      /**
       * Get the primitive value of the Range, a one dimensional array
       * @memberof Range
       * @returns {Array} array
       */
      valueOf(): number[] {
        // TODO: implement a caching mechanism for range.valueOf()
        return this.toArray()
      }

      /**
       * Get a string representation of the range, with optional formatting options.
       * Output is formatted as 'start:step:end', for example '2:6' or '0:0.2:11'
       * @memberof Range
       * @param {Object | number | function} [options] Formatting options. See
       *                                               lib/utils/number:format for a
       *                                               description of the available
       *                                               options.
       * @returns {string} str
       */
      format(
        options?: RangeFormatOptions | number | ((value: number) => string)
      ): string {
        let str = format(this.start, options)

        if (this.step !== 1) {
          str += ':' + format(this.step, options)
        }
        str += ':' + format(this.end, options)
        return str
      }

      /**
       * Get a string representation of the range.
       * @memberof Range
       * @returns {string}
       */
      toString(): string {
        return this.format()
      }

      /**
       * Get a JSON representation of the range
       * @memberof Range
       * @returns {Object} Returns a JSON object structured as:
       *                   `{"mathjs": "Range", "start": 2, "end": 4, "step": 1}`
       */
      toJSON(): RangeJSON {
        return {
          mathjs: 'Range',
          start: this.start,
          end: this.end,
          step: this.step
        }
      }

      /**
       * Instantiate a Range from a JSON object
       * @memberof Range
       * @param {Object} json A JSON object structured as:
       *                      `{"mathjs": "Range", "start": 2, "end": 4, "step": 1}`
       * @return {Range}
       */
      static fromJSON(json: RangeJSON): Range {
        return new Range(json.start, json.end, json.step)
      }
    }

    // Set prototype properties for type checking (duck typing)
    // These are needed because is.ts checks constructor.prototype.isRange
    ;(Range.prototype as any).type = 'Range'
    ;(Range.prototype as any).isRange = true

    return Range
  },
  { isClass: true }
)
