import { factory } from '../../utils/factory.ts'
import type { TypedFunction } from '../../core/function/typed.ts'
import { createMatAlgo01xDSid } from '../../type/matrix/utils/matAlgo01xDSid.ts'
import { createMatAlgo02xDS0 } from '../../type/matrix/utils/matAlgo02xDS0.ts'
import { createMatAlgo06xS0S0 } from '../../type/matrix/utils/matAlgo06xS0S0.ts'
import { createMatAlgo11xS0s } from '../../type/matrix/utils/matAlgo11xS0s.ts'
import { createMatrixAlgorithmSuite } from '../../type/matrix/utils/matrixAlgorithmSuite.ts'
import { nthRootNumber } from '../../plain/number/index.ts'

const name = 'nthRoot'
const dependencies = ['typed', 'matrix', 'equalScalar', 'BigNumber', 'concat']

export const createNthRoot = /* #__PURE__ */ factory(
  name,
  dependencies,
  ({
    typed,
    matrix,
    equalScalar,
    BigNumber,
    concat
  }: {
    typed: TypedFunction
    matrix: any
    equalScalar: any
    BigNumber: any
    concat: any
  }): any => {
    const matAlgo01xDSid = createMatAlgo01xDSid({ typed })
    const matAlgo02xDS0 = createMatAlgo02xDS0({ typed, equalScalar })
    const matAlgo06xS0S0 = createMatAlgo06xS0S0({ typed, equalScalar })
    const matAlgo11xS0s = createMatAlgo11xS0s({ typed, equalScalar })
    const matrixAlgorithmSuite = createMatrixAlgorithmSuite({
      typed,
      matrix,
      concat
    })

    /**
     * Calculate the nth root of a value.
     * The principal nth root of a positive real number A, is the positive real
     * solution of the equation
     *
     *     x^root = A
     *
     * For matrices, the function is evaluated element wise.
     *
     * Syntax:
     *
     *     math.nthRoot(a)
     *     math.nthRoot(a, root)
     *
     * Examples:
     *
     *     math.nthRoot(9, 2)    // returns 3 (since 3^2 == 9)
     *     math.sqrt(9)          // returns 3 (since 3^2 == 9)
     *     math.nthRoot(64, 3)   // returns 4 (since 4^3 == 64)
     *
     * See also:
     *
     *     sqrt, pow
     *
     * @param {number | BigNumber | Array | Matrix | Complex} a
     *              Value for which to calculate the nth root
     * @param {number | BigNumber} [root=2]    The root.
     * @return {number | Complex | Array | Matrix} Returns the nth root of `a`
     */
    function complexErr(): never {
      throw new Error(
        'Complex number not supported in function nthRoot. Use nthRoots instead.'
      )
    }

    return typed(
      name,
      {
        number: nthRootNumber,
        'number, number': nthRootNumber,

        BigNumber: (x: any): any => _bigNthRoot(x, new BigNumber(2)),
        'BigNumber, BigNumber': _bigNthRoot,

        Complex: complexErr,
        'Complex, number': complexErr,

        Array: (typed as any).referTo(
          'DenseMatrix,number',
          (selfDn: any) =>
            (x: any): any =>
              selfDn((matrix as any)(x), 2).valueOf()
        ),
        DenseMatrix: (typed as any).referTo(
          'DenseMatrix,number',
          (selfDn: any) =>
            (x: any): any =>
              selfDn(x, 2)
        ),
        SparseMatrix: (typed as any).referTo(
          'SparseMatrix,number',
          (selfSn: any) =>
            (x: any): any =>
              selfSn(x, 2)
        ),

        'SparseMatrix, SparseMatrix': typed.referToSelf(
          (self: any) =>
            (x: any, y: any): any => {
              // density must be one (no zeros in matrix)
              if (y.density() === 1) {
                // sparse + sparse
                return matAlgo06xS0S0(x, y, self)
              } else {
                // throw exception
                throw new Error('Root must be non-zero')
              }
            }
        ),

        'DenseMatrix, SparseMatrix': typed.referToSelf(
          (self: any) =>
            (x: any, y: any): any => {
              // density must be one (no zeros in matrix)
              if (y.density() === 1) {
                // dense + sparse
                return matAlgo01xDSid(x, y, self, false)
              } else {
                // throw exception
                throw new Error('Root must be non-zero')
              }
            }
        ),

        'Array, SparseMatrix': (typed as any).referTo(
          'DenseMatrix,SparseMatrix',
          (selfDS: any) =>
            (x: any, y: any): any =>
              selfDS((matrix as any)(x), y)
        ),

        'number | BigNumber, SparseMatrix': typed.referToSelf(
          (self: any) =>
            (x: any, y: any): any => {
              // density must be one (no zeros in matrix)
              if (y.density() === 1) {
                // sparse - scalar
                return matAlgo11xS0s(y, x, self, true)
              } else {
                // throw exception
                throw new Error('Root must be non-zero')
              }
            }
        )
      },
      matrixAlgorithmSuite({
        scalar: 'number | BigNumber',
        SD: matAlgo02xDS0,
        Ss: matAlgo11xS0s,
        sS: false
      })
    )

    /**
     * Calculate the nth root of a for BigNumbers, solve x^root == a
     * https://rosettacode.org/wiki/Nth_root#JavaScript
     * @param {BigNumber} a
     * @param {BigNumber} root
     * @private
     */
    function _bigNthRoot(a: any, root: any): any {
      const precision = BigNumber.precision
      const Big = BigNumber.clone({ precision: precision + 2 })
      const zero = new BigNumber(0)

      const one = new Big(1)
      const inv = root.isNegative()
      if (inv) {
        root = root.neg()
      }

      if (root.isZero()) {
        throw new Error('Root must be non-zero')
      }
      if (a.isNegative() && !root.abs().mod(2).equals(1)) {
        throw new Error('Root must be odd when a is negative.')
      }

      // edge cases zero and infinity
      if (a.isZero()) {
        return inv ? new Big(Infinity) : 0
      }
      if (!a.isFinite()) {
        return inv ? zero : a
      }

      let x = a.abs().pow(one.div(root))
      // If a < 0, we require that root is an odd integer,
      // so (-1) ^ (1/root) = -1
      x = a.isNeg() ? x.neg() : x
      return new BigNumber((inv ? one.div(x) : x).toPrecision(precision))
    }
  }
)

export const createNthRootNumber = /* #__PURE__ */ factory(
  name,
  ['typed'] as const,
  ({ typed }: { typed: any }): any => {
    return typed(name, {
      number: nthRootNumber,
      'number, number': nthRootNumber
    })
  }
)
