import { factory } from '../utils/factory.ts'
import { deepMap } from '../utils/collection.ts'
import type { Decimal } from 'decimal.js'

const name = 'boolean'
const dependencies = ['typed']

export const createBoolean = /* #__PURE__ */ factory(
  name,
  dependencies,
  ({ typed }: { typed: any }) => {
    /**
     * Create a boolean or convert a string or number to a boolean.
     * In case of a number, `true` is returned for non-zero numbers, and `false` in
     * case of zero.
     * Strings can be `'true'` or `'false'`, or can contain a number.
     * When value is a matrix, all elements will be converted to boolean.
     *
     * Syntax:
     *
     *    math.boolean(x)
     *
     * Examples:
     *
     *    math.boolean(0)     // returns false
     *    math.boolean(1)     // returns true
     *    math.boolean(-3)     // returns true
     *    math.boolean('true')     // returns true
     *    math.boolean('false')     // returns false
     *    math.boolean([1, 0, 1, 1])     // returns [true, false, true, true]
     *
     * See also:
     *
     *    bignumber, complex, index, matrix, string, unit
     *
     * @param {string | number | boolean | Array | Matrix | null} value  A value of any type
     * @return {boolean | Array | Matrix} The boolean value
     */
    return typed(name, {
      '': function (): boolean {
        return false
      },

      boolean: function (x: boolean): boolean {
        return x
      },

      number: function (x: number): boolean {
        return !!x
      },

      null: function (_x: null): boolean {
        return false
      },

      BigNumber: function (x: Decimal): boolean {
        return !x.isZero()
      },

      string: function (x: string): boolean {
        // try case insensitive
        const lcase = x.toLowerCase()
        if (lcase === 'true') {
          return true
        } else if (lcase === 'false') {
          return false
        }

        // test whether value is a valid number
        const num = Number(x)
        if (x !== '' && !isNaN(num)) {
          return !!num
        }

        throw new Error('Cannot convert "' + x + '" to a boolean')
      },

      'Array | Matrix': typed.referToSelf(
        (self: (x: any) => any) => (x: any) => deepMap(x, self)
      )
    })
  }
)
