import { isNode } from '../../utils/is.ts'
import { forEach, map } from '../../utils/array.ts'
import { factory } from '../../utils/factory.ts'
import type { MathNode } from './Node.ts'

const name = 'BlockNode'
const dependencies = ['ResultSet', 'Node']

interface BlockItem {
  node: MathNode
  visible: boolean
}

export const createBlockNode = /* #__PURE__ */ factory(
  name,
  dependencies,
  ({
    ResultSet,
    Node
  }: {
    ResultSet: any
    Node: new (...args: any[]) => MathNode
  }) => {
    class BlockNode extends Node {
      blocks: BlockItem[]

      /**
       * @constructor BlockNode
       * @extends {Node}
       * Holds a set with blocks
       * @param {Array.<{node: Node} | {node: Node, visible: boolean}>} blocks
       *            An array with blocks, where a block is constructed as an
       *            Object with properties block, which is a Node, and visible,
       *            which is a boolean. The property visible is optional and
       *            is true by default
       */
      constructor(blocks: Array<{ node: MathNode; visible?: boolean }>) {
        super()
        // validate input, copy blocks
        if (!Array.isArray(blocks)) throw new Error('Array expected')
        this.blocks = blocks.map(function (block) {
          const node = block && block.node
          const visible =
            block && block.visible !== undefined ? block.visible : true

          if (!isNode(node))
            throw new TypeError('Property "node" must be a Node')
          if (typeof visible !== 'boolean') {
            throw new TypeError('Property "visible" must be a boolean')
          }

          return { node, visible }
        })
      }

      get type(): string {
        return name
      }
      get isBlockNode(): boolean {
        return true
      }

      /**
       * Compile a node into a JavaScript function.
       * This basically pre-calculates as much as possible and only leaves open
       * calculations which depend on a dynamic scope with variables.
       * @param {Object} math     Math.js namespace with functions and constants.
       * @param {Object} argNames An object with argument names as key and `true`
       *                          as value. Used in the SymbolNode to optimize
       *                          for arguments from user assigned functions
       *                          (see FunctionAssignmentNode) or special symbols
       *                          like `end` (see IndexNode).
       * @return {function} Returns a function which can be called like:
       *                        evalNode(scope: Object, args: Object, context: *)
       */
      _compile(
        math: any,
        argNames: Record<string, boolean>
      ): (scope: any, args: any, context: any) => any {
        const evalBlocks = map(this.blocks, function (block) {
          return {
            evaluate: block.node._compile(math, argNames),
            visible: block.visible
          }
        })

        return function evalBlockNodes(scope: any, args: any, context: any) {
          const results: any[] = []

          forEach(evalBlocks, function evalBlockNode(block: any) {
            const result = block.evaluate(scope, args, context)
            if (block.visible) {
              results.push(result)
            }
          })

          return new ResultSet(results)
        }
      }

      /**
       * Execute a callback for each of the child blocks of this node
       * @param {function(child: Node, path: string, parent: Node)} callback
       */
      forEach(
        callback: (child: MathNode, path: string, parent: MathNode) => void
      ): void {
        for (let i = 0; i < this.blocks.length; i++) {
          callback(this.blocks[i].node, 'blocks[' + i + '].node', this as any)
        }
      }

      /**
       * Create a new BlockNode whose children are the results of calling
       * the provided callback function for each child of the original node.
       * @param {function(child: Node, path: string, parent: Node): Node} callback
       * @returns {BlockNode} Returns a transformed copy of the node
       */
      map(
        callback: (child: MathNode, path: string, parent: MathNode) => MathNode
      ): BlockNode {
        const blocks: BlockItem[] = []
        for (let i = 0; i < this.blocks.length; i++) {
          const block = this.blocks[i]
          const node = this._ifNode(
            callback(block.node, 'blocks[' + i + '].node', this as any)
          )
          blocks[i] = {
            node,
            visible: block.visible
          }
        }
        return new BlockNode(blocks)
      }

      /**
       * Create a clone of this node, a shallow copy
       * @return {BlockNode}
       */
      clone(): BlockNode {
        const blocks = this.blocks.map(function (block) {
          return {
            node: block.node,
            visible: block.visible
          }
        })

        return new BlockNode(blocks)
      }

      /**
       * Get string representation
       * @param {Object} options
       * @return {string} str
       * @override
       */
      _toString(options?: any): string {
        return this.blocks
          .map(function (param) {
            return param.node.toString(options) + (param.visible ? '' : ';')
          })
          .join('\n')
      }

      /**
       * Get a JSON representation of the node
       * @returns {Object}
       */
      toJSON(): { mathjs: string; blocks: BlockItem[] } {
        return {
          mathjs: name,
          blocks: this.blocks
        }
      }

      /**
       * Instantiate an BlockNode from its JSON representation
       * @param {Object} json
       *     An object structured like
       *     `{"mathjs": "BlockNode", blocks: [{node: ..., visible: false}, ...]}`,
       *     where mathjs is optional
       * @returns {BlockNode}
       */
      static fromJSON(json: {
        blocks: Array<{ node: MathNode; visible?: boolean }>
      }): BlockNode {
        return new BlockNode(json.blocks)
      }

      /**
       * Get HTML representation
       * @param {Object} options
       * @return {string} str
       * @override
       */
      _toHTML(options?: any): string {
        return this.blocks
          .map(function (param) {
            return (
              param.node.toHTML(options) +
              (param.visible ? '' : '<span class="math-separator">;</span>')
            )
          })
          .join('<span class="math-separator"><br /></span>')
      }

      /**
       * Get LaTeX representation
       * @param {Object} options
       * @return {string} str
       */
      _toTex(options?: any): string {
        return this.blocks
          .map(function (param) {
            return param.node.toTex(options) + (param.visible ? '' : ';')
          })
          .join('\\;\\;\n')
      }
    }

    // Set the class name to match the node type
    // Using Object.defineProperty because Function.name is read-only
    Object.defineProperty(BlockNode, 'name', {
      value: name,
      configurable: true
    })

    return BlockNode
  },
  { isClass: true, isNode: true }
)
